<template id="todo-item">
	<li>
		<span>{{ todo.text }}</span>
		<button @click="removeTodoItem(todo.id)">X</button>
	</li>
</template>

<script>
export default {
	props: {
		todo: {
			type: Object,
			required: true
		}
	},
	methods: {
		removeTodoItem: function(id) {
			this.$emit('removeTodo', id);
		}
	}
}
</script>

<style scoped>
#todo-item {
	
}
</style>


