<template>
	<div>
		<h1>Things to do!!!</h1>
		<input type="text" placeholder="new todo" v-model="newTodoItemText" @keydown.enter="addTodo">
		<ul v-if="todolist.length">
			<TodoListItem v-for="todo in todolist" :key="todo.id" :todo="todo" @removeTodo="removeTodo" />
		</ul>
		<p v-else>
			Nothing to do now.
		</p>
	</div>
</template>

<script>
import TodoListItem from './TodoListItem.vue'

let nextTodoId = 1

export default {
	name: 'TodoList',
	components: {
		TodoListItem
	},
	data:function() {
		return {
			newTodoItemText: '',
			todolist: []
		}
	},
	methods: {
		addTodo: function() {
			var todoItem = {
				id: nextTodoId++,
				text: this.newTodoItemText.trim()
			};
			this.todolist.push(todoItem);
			this.newTodoItemText = '';
		},
		removeTodo: function(removeId) {
			// 数组过滤某些不合条件的元素，可用于删除元素
			this.todolist = this.todolist.filter(
				/*function(todo) {
					return todo.id !== removeId;
				}*/
				todo => {
					return todo.id !== removeId;
				}
			);
		}
	}
}
</script>

<style>
	
</style>


